#title_wrapper {
    display: table;
    height: 100vh;
}

#title_content_wrapper {
    display: table-cell;
    vertical-align: middle;
}

.name_title {
    font-size: 15vmin;
    text-align: center;

    margin: 0;

    color: #FFFDFD;
}

.role_title {
    font-size: 5vmin;
    text-align: center;

    margin: 0;
    margin-bottom: 10px;

    color: #F0E100;
}

.flashing {
    animation-name: monoscroll;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    animation-iteration-count: infinite;
}

@keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#options_table {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    table-layout: fixed;
    text-align: center;
}

.option_text {
    color: white;
    font-size: 5vmin;
}

.option_text a {
    color: white;
    text-decoration: none;
}

.option_text a:hover {
    text-decoration: overline;
}