@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
    font-family: 'Montserrat', serif;

    background-image: linear-gradient(to bottom, rgba(31, 31, 31, 0.4) 0%, rgba(31, 31, 31, 0.4) 100%), url(https://source.unsplash.com/1920x1080/?+mountain);
    background-attachment: fixed;
}

#root {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
}

#page_wrapper {
    width: 100%;
    height: 100vh;
    display: table;
    margin: 0;
    padding: 0
}

.full_section_wrapper {
    width: 100%;
}

.sub_section_wrapper {
    width: 100%;
}

#image_tag {
    position: fixed;
    left: 0;
    top: 0;

    padding-left: 15px;
}

#image_tag_text {
    color: white;
}

#image_tag_text a {
    color: white;
    text-decoration: overline;
}
#title_wrapper {
    display: table;
    height: 100vh;
}

#title_content_wrapper {
    display: table-cell;
    vertical-align: middle;
}

.name_title {
    font-size: 15vmin;
    text-align: center;

    margin: 0;

    color: #FFFDFD;
}

.role_title {
    font-size: 5vmin;
    text-align: center;

    margin: 0;
    margin-bottom: 10px;

    color: #F0E100;
}

.flashing {
    -webkit-animation-name: monoscroll;
            animation-name: monoscroll;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#options_table {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    table-layout: fixed;
    text-align: center;
}

.option_text {
    color: white;
    font-size: 5vmin;
}

.option_text a {
    color: white;
    text-decoration: none;
}

.option_text a:hover {
    text-decoration: overline;
}
